@import "./responsive";
@import url("https://fonts.googleapis.com/css?family=Open+Sans&display=swap"); // Google Fonts Import

// $font-stack: Roboto,sans-serif;
$font-stack: NeufileGrotesk, Helvetica, Arial, sans-serif;
$primary-color: #95ca31;
$green-color: #53da89;
$dark-color: #2b2c2c; //4a4a4a
$light-blue-color: #419bff;
$light-gray-color: #fafafa;
$gray-color: #dfe6e9;
$red-color: #d63031;
$large-heading: 6.5vh;
$heading: 32px;
$sub-heading: 24px;
$font-pera: 16px;
$font-mobile: 12px;

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

.ant-menu-horizontal > .ant-menu-item:hover,
.ant-menu-horizontal > .ant-menu-submenu:hover,
.ant-menu-horizontal > .ant-menu-item-active,
.ant-menu-horizontal > .ant-menu-submenu-active,
.ant-menu-horizontal > .ant-menu-item-open,
.ant-menu-horizontal > .ant-menu-submenu-open,
.ant-menu-horizontal > .ant-menu-item-selected,
.ant-menu-horizontal > .ant-menu-submenu-selected {
  border-bottom: 2px solid $primary-color;
  color: $primary-color;
}

@include desktop-and-up {
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 0px;
    background-color: #f5f5f5;
  }

  ::-webkit-scrollbar {
    width: 4px;
    background-color: #f5f5f5;
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 0px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #555;
  }
}

.ant-slider-handle {
  height: 25px;
  width: 25px;
  margin-top: -10px;
}

.pointer {
  cursor: pointer;
}

.color-icon {
  color: $primary-color !important;
}
.margin-0 {
  margin: 0;
}
.icon-padding {
  padding: 15px 0;
}

.heading-large {
  font-size: 30px;
  font-weight: 800;
  color: black;
}

.heading-medium {
  font-size: 20px;
  font-weight: 600;
  color: black;
}
.heading-regular {
  font-size: 20px;
  font-weight: 400;
  color: #2a2a2a !important;

  @include mobile-only {
    padding: 0% 2%;
  }
}

.ant-modal-close-x {
  font-size: 25px;
  font-weight: 100;
}

.font-capitalize {
  text-transform: capitalize;
}

.ant-tabs-nav .ant-tabs-tab-active {
  color: black;
}

.ant-tabs-ink-bar {
  background-color: $primary-color;
}

.align-center {
  text-align: center;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.font-size-icons {
  font-size: 10px !important;
  margin-bottom: 5px;
  vertical-align: middle;
}

.font-light-color {
  color: #7f8c8d;
}

.font-black-color {
  color: black;
}

.ant-message {
  z-index: 9999 !important;
}

.ant-dropdown {
  z-index: 100000 !important;
}

.animate-opacity {
  animation: opacity 0.5s ease-in-out;
}
.animate-left {
  animation: left 0.5s;
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes left {
  0% {
    margin-left: -1000px;
  }
  100% {
    margin-left: 0;
  }
}

.ant-select-dropdown {
  max-height: 450px;
  overflow: auto;

  .ant-select-dropdown-menu {
    max-height: -webkit-fill-available;
  }

  &.sticky {
    position: fixed;
    top: 60px !important;
    left: 25% !important;
    width: 50% !important;
    z-index: 9999;
  }
}
.app-modal {
  .connect-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .rowItem {
      margin-top: 40px;
    }

    .store-wrapper {
      display: flex;
      flex-direction: row;

      @include mobile-only {
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      .ambii-logo {
        width: 135px;
        height: 135px;
        border-radius: 8px;

        @include mobile-only {
          margin-bottom: 20px;
        }
      }

      .store-button-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: 20px;

        @include mobile-only {
          margin: 0px;
        }

        .store-button {
          display: flex;
          flex-direction: row;
          width: max-content;
          background-color: grey;
          border: 2px solid lightgrey;
          border-radius: 8px;
          justify-content: flex-start;
          align-items: center;
          padding: 8px 12px;
          height: 58px;
          width: 175px;
          transition: 0.2s ease;
          cursor: pointer;
          color: white;

          &:first-child {
            margin-bottom: 20px;
          }

          &:hover {
            opacity: 0.8;
            background-color: white;
            color: grey;
          }

          .store-button-subtitle {
            font-size: 13px;
          }

          .store-button-title {
            font-size: 18px;
            line-height: 1;
          }

          .brand-icon {
            margin-right: 15px;
          }
        }
      }
    }

    .connect-button-group {
      display: flex;
      flex-direction: row;
      margin-top: 50px;

      @include mobile-only {
        flex-direction: column;

        .mobile-padding {
          margin-top: 15px;
        }
      }
    }

    .connect-header {
      font-size: 32px;
      font-weight: bold;
      color: grey;

      @include mobile-only {
        font-size: 28px;
        text-align: center;
      }
    }

    .connect-text {
      font-size: 18px;
      color: grey;
      text-align: center;
      width: 75%;

      @include mobile-only {
        font-size: 16px;
      }

      .connect-token {
        color: #d04283;
        font-weight: bold;
        letter-spacing: 6px;
      }
    }

    .connect-display {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;

      @include mobile-only {
        flex-direction: column;
        display: none;
      }

      .connect-icon {
        font-size: 96px;
        color: grey;

        @include mobile-only {
          font-size: 56px;
        }
      }

      .mobile-phone {
        position: relative;

        .special-music-icon {
          position: absolute;
          font-size: 24px;
          color: grey;
          top: 24px;
          left: 17px;

          @include mobile-only {
            font-size: 16px;
            top: 12px;
            left: 9px;
          }
        }
      }

      .speaker-group {
        display: flex;
        flex-direction: row;
        align-items: center;

        .connect-music-icon {
          font-size: 45px;
          color: #d04283;
          margin-left: 10px;
          align-self: flex-start;

          @include mobile-only {
            font-size: 36px;
          }
        }
      }

      .connect-dashes {
        font-size: 24px;
        color: lightgrey;
        margin: 0 20px;
      }
    }

    .animate-right {
      animation: right 0.5s ease-in-out;
    }
    .animate-left {
      animation: left 0.5s ease-in-out;
    }
    .animate-opacity {
      animation: opacity 0.5s ease-in-out;
    }
  }

  .ant-modal-footer {
    display: none;
  }
}
