@import "../../../common//styles/styles.scss";
@import "../../../common/styles/responsive";

.discoverWrapper {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;

  .create-body {
    font-weight: 600;

    input:focus {
      -webkit-box-shadow: 0 0 10px 1px #096dd9 !important;
      box-shadow: 0 0 10px 1px #096dd9 !important;
    }

    .autocomplete-wrapper {
      width: 80%;
    }

    @include mobile-only {
      padding-top: 5%;
    }

    .create-list-view {
      display: flex;
      flex: 1;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end;
      padding-top: 30px;
      min-height: 10px;
      margin: 0% 5%;

      @include mobile-only {
        width: calc(100vw - 10%);
        padding-top: 0px;
        justify-content: center;
        align-items: center;
        font-size: 1.5em;
        flex-direction: column;
        text-align: center;
      }

      .create-list-view-title {
        font-size: 48px;
		margin-left: -3px; // visually aligns with the border better
		line-height: 1;

        @include mobile-only {
          font-size: 28px;
        }
      }

      .create-list-view-subtitle {
        display: flex;
        flex-direction: row;
        font-size: 18px;

        @include mobile-only {
          flex-direction: column;
          text-align: center;
		  justify-content: center;
		  margin-top: 15px;
        }

        .advanced-button {
          color: #d04283;
          cursor: pointer;
          opacity: 1;
          transition: 0.2s ease;
          display: flex;
          align-items: center;
          margin-left: 8px;

          &:hover {
            opacity: 0.8;
          }

          @include mobile-only {
            justify-content: center;
            margin-left: 0px;
            margin-bottom: 20px;
          }
        }
      }

      .discover-buttons {
        display: flex;
        flex-direction: row;
        justify-content: center;
        padding-bottom: 8px;

        .viewOptions {
          display: flex;
          flex-direction: column;
          align-items: center;
          padding-right: 10px;

          .browseButton {
            margin-top: 8px;
          }
        }

        .preference-menu {
          border: none !important;
          background-color: transparent !important;
          color: #d04283 !important;
          box-shadow: none !important;
          transition: none !important;
          outline: none !important;
          outline-color: none !important;
          font-family: var(--default-font);
        }
        .preference-menu-button {
          font-size: 16px !important;
        }

        img {
          width: 347px;
          display: flex;
          align-self: center;
        }
      }

      .sticky {
        position: fixed;
        top: 12px;
        left: 25%;
        width: 50%;
        z-index: 9999;
        animation: opacity 0.2s ease;
      }
    }

    .browseSettingsContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 15px;
      margin-bottom: 30px;

      .browseSettingsTitle {
        font-size: 24px;
        margin-top: 25px;
      }

      .browseSelectionsContainer {
        width: 85%;
        max-width: 1100px;
        margin-bottom: 20px;

        .browseSelectionsButtons {
          margin: 5px;
          border-color: #d04283;
          border-width: 0px;
        }
      }

      .playlistsContainer {
        display: grid;
        width: 100%;
        justify-content: center;
        grid-template-columns: repeat(auto-fit, calc(max(300px, 22vw)));
        row-gap: 15px;

        .playlistContainer {
          width: fit-content;
          margin: auto;
        }
      }
    }
  }
}

.nowPlayingWrapper {
  display: flex;
  flex: 1;
  padding: 10px 0px 10px 0px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 0px 5%;

  .titleContainer {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-self: center;
    flex-direction: row;
	margin: 10px 0px;

    @include mobile-only {
      justify-content: center;
    }

    .create-list-view {
      display: flex;
      flex: 1;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end;
      min-height: 10px;

      @include mobile-only {
        width: calc(100vw - 10%);
        padding-top: 0px;
        justify-content: center;
        align-items: center;
        font-size: 1.5em;
        flex-direction: column;
        text-align: center;
      }

      .backArrow {
        font-size: 24px;
        color: lightgrey;
        cursor: pointer;
		transition: 0.2s ease;
		margin-right: 20px;

        &:hover {
          opacity: 0.8;
          color: grey;
        }

        @include mobile-only {
          display: none;
        }
      }

      .create-list-view-title {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 48px;
        font-weight: bold;
        margin-left: -3px; // visually aligns with the border better
		
        @include mobile-only {
          justify-content: center;
          font-size: 32px;
        }
      }

      .create-list-view-subtitle {
        display: flex;
        font-size: 18px;

        @include mobile-only {
          flex-direction: column;
          justify-content: center;
        }

        .advanced-button {
          color: #d04283;
          cursor: pointer;
          opacity: 1;
          transition: 0.2s ease;
          display: flex;
          align-items: center;
          margin-left: 8px;

          &:hover {
            opacity: 0.8;
          }
        }
      }
    }
  }

  .nowPlayingContainer {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    background-color: white;
    box-shadow: 0px 3px 3px grey;
    border-radius: 10px;
    margin-top: 40px;
    position: relative;

    @include mobile-only {
      align-items: baseline;
      flex-direction: column;
      padding: 0px 20px 20px 20px;
    }

    .imgAndInfo {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding-right: 40px;

      @include mobile-only {
        width: 100%;
        justify-content: center;
        padding-right: 0px;
        flex-direction: column;
      }

      .adblock-div {
        display: flex;
        position: absolute;
        top: 20px;
        right: 20px;
        color: #d04283af;
        font-size: 32px;
        transform: scale(1);
        animation: pulse 2s infinite;
        border-radius: 50%;
        cursor: pointer;
        opacity: 1;
        transition: 0.2s ease;

        &:hover {
          opacity: 0.8;
        }

        @include mobile-only {
          display: none;
        }
      }

      @keyframes pulse {
        0% {
          transform: scale(0.95);
        }
        70% {
          transform: scale(1.15);
        }
        100% {
          transform: scale(0.95);
        }
      }

      .songImageContainer {
        display: flex;
        margin-top: -25px;
        margin-bottom: 25px;
        background-color: white;
        margin-left: 40px;
        border-radius: 10px;
        box-shadow: 0px 3px 3px grey;

        @include mobile-only {
          margin-left: 0px;
        }

        .songImage {
          display: flex;
          width: 250px;
          height: 250px;
          padding: 10px;
          object-fit: contain;

          @include mobile-only {
            width: 150px;
            height: 150px;
          }
        }
      }

      .infoContainer {
        margin-left: 20px;
        color: black;
        font-size: 15px;
        min-width: 200px;

        @include mobile-only {
          font-size: 12px;
          text-align: center;
          margin: 0px;
        }

        .nowPlayingTitle {
          display: flex;
          box-shadow: none;
          margin-top: 0px;
          flex-direction: row;
          align-items: center;

          @include mobile-only {
            justify-content: center;
          }

          .beat-container {
            margin-left: 12px;
            width: 10px;
            height: 10px;
            background-color: #d04283;
            border-radius: 20px;
          }

          @keyframes bpm {
            0% {
              box-shadow: 0 0 0 0 rgba(208, 66, 131, 1);
            }
            100% {
              box-shadow: 0 0 0 10px rgba(193, 120, 255, 0);
              background-color: #c178ff;
            }
          }
        }

        .songTitle {
          font-size: 24px;
          font-weight: bold;

          @include mobile-only {
            font-size: 18px;
          }
        }
      }
    }

    .playButton-wrapper {
      flex: 1;
      justify-content: flex-end;
      margin: 10px 40px;

      @include mobile-only {
        width: 100%;
        justify-content: center;
        margin: 20px 0px 0px 0px;
      }

      .playButton {
        background: linear-gradient(330deg, rgba(208, 66, 131, 1) 0%, rgba(193, 120, 255, 1) 100%);
        border-radius: 1000px;
        border-color: lightgrey;
        border-width: 3px;
        border-style: solid;
        cursor: pointer;
        transition: 0.25s opacity;

        &:hover {
          opacity: 0.85;
        }
      }
    }
  }

  .modulesContainer {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 30px;

    .moduleContainer {
      display: flex;
      min-width: 280px;
      background-color: white;
      border-radius: 10px;
      box-shadow: 0px 3px 3px grey;
      flex-direction: column;
      padding: 15px;

      .storeCodeTitle {
        margin-top: 5px;
        font-size: 20px;
        text-align: center;
      }

      .storeCode {
        display: flex;
        justify-content: center;
        margin-top: 5px;
      }

      .connectButton {
        display: flex;
        align-items: center;
        background-image: linear-gradient(to right, #d04283, #c178ff);
        margin: 10px 20px;
        margin-top: 20px;
        border-radius: 8px;
        height: 80px;
        cursor: pointer;
        transition: 0.25s opacity;
        box-shadow: 0px 3px 3px grey;

        &:hover {
          opacity: 0.8;
        }

        .connectButtonText {
          color: white;
          font-size: 17px;
          width: 120px;
          text-align: center;
          margin: auto;
        }
      }
    }
  }

  .businessName {
    font-size: 32px;
    color: #2a2a2a;
  }

  .stationName {
    font-size: 18px;
    margin-bottom: 5px;
  }

  .songTrack {
    font-size: 18px;
    margin-bottom: 2px;
  }

  .songArtist {
    font-size: 14px;
    margin-bottom: 25px;
  }

  .playerCode {
    margin-bottom: 50px;
  }

  .animate-right {
    animation: right 0.5s ease-in-out;
  }
  .animate-left {
    animation: left 0.5s ease-in-out;
  }
  .animate-opacity {
    animation: opacity 0.5s ease-in-out;
  }
}

.ant-collapse-header {
  @include mobile-only {
    padding: 0 !important;
  } // Centers the searchbal Panel on mobile view
}

@keyframes bg {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes bottom {
  0% {
    top: 40px;
  }
  100% {
    top: 15px;
  }
}

@keyframes left {
  0% {
    margin-left: -160px;
  }

  100% {
    margin-left: 0;
  }
}

@keyframes right {
  0% {
    margin-right: -160px;
  }
  100% {
    margin-right: 0;
  }
}

.ant-collapse-item {
  border: none !important;
}

.logo-with-text {
  height: 100px;
  margin-bottom: 20px;
}

#discover-container {
  transition: background 0.5s ease-in-out;
}
/* colours */
.genre-active {
  background: #63b0cd !important;
}
.mood-active {
  background: #f67e7d !important;
}
.era-active {
  background: #d0ce7c !important;
}
