@font-face {
  font-family: "Rounded Elegance";
  src: url(./fonts/Rounded_Elegance.ttf) format("truetype");
}

// Global Variables
:root {
  --navbar-height: 64px;
  --footer-height: 250px;
  --content-height: calc(100vh - var(--navbar-height) - var(--footer-height));
  --default-font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

body {
  margin: 0;
  font-family: var(--default-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

// Icon global vals
.icon {
  display: flex;
  cursor: pointer;
  transition: 0.2s ease;

  &:hover {
    opacity: 0.8;
  }
}

// Override headers
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 !important;
}
