@import "./common/styles/responsive";

.loading-app {
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;

  .loading-app-text {
    font-size: 24px;
    margin-bottom: 20px;
    font-size: bold;
  }
}

.explore_container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.covid-banner {
  display: flex;
  width: 100%;
  min-height: 45px;
  background-image: linear-gradient(to right, #d04283, #c178ff);
  color: white;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  letter-spacing: 1px;
  padding: 5px 5%;

  a {
    color: white;
    text-decoration: underline;
    transition: 0.2s ease;
    margin-left: 5px;
    &:hover {
      opacity: 0.7;
    }
  }

  @include mobile-only {
    padding: 5px 5%;
    text-align: center;
    font-size: 12px;
    letter-spacing: 0px;

    a {
      margin-left: 0px;
    }
  }
}

.contentWrapper {
  display: flex;
  flex: 1;
}

.player-visible {
	margin-bottom: 90px; // Adds some extra view space so smaller screens can see the page bottom
}

AppFooter {
  display: flex;
  flex: 1;
}

.ant-table {
  margin-bottom: 25px !important;
}

.ant-layout {
  background-color: transparent;
}

.ant-input,
input {
  border-radius: 4px !important;
  padding: 5px !important;
  border: 1px solid rgb(217, 217, 217);
}

.flexRow {
  display: flex;
  flex-direction: row;
}

.flexCol {
  display: flex;
  flex-direction: column;
}
