@import "../../common/styles/responsive.scss";

.music-bar-wrapper {
  position: fixed;
  bottom: 0px;
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 75px;
  background-color: rgba(255, 255, 255, 0.95);

  .react-music-player {
    display: none;
  }

  .music-bar-scrub-wrapper {
    width: 100%;
    background-color: grey;
    height: 3px;

    .music-bar-scrubber {
      width: 0%;
      height: 100%;
      background-image: linear-gradient(to right, #d04283, #c178ff);
      transition: 0.1s ease;
    }
  }

  .music-bar-content-wrapper {
    display: flex;
    flex: 1;
    align-items: center;
    padding: 0 5%;

    .music-bar-item {
      display: flex;
      flex: 1;

      .preview-image {
        width: 45px;
        margin-right: 15px;
        border-radius: 4px;
      }

      .preview-text {
        display: flex;
        flex-direction: column;

        @include mobile-only {
          display: none;
        }
      }

      .song-artist {
        font-size: 14px;
        color: grey;
      }

      .song-title {
        font-size: 16px;
      }

      .music-bar-icon {
        font-size: 24px;
        margin-left: 25px;
        transition: 0.2s ease;
        cursor: pointer;

        &:hover {
          opacity: 0.8;
        }
      }

      .disabled {
        opacity: 0.2;
        cursor: default;

        &:hover {
          opacity: 0.2;
        }
      }

      .close-icon {
        font-size: 32px;
      }

      .volume-icon {
        display: flex;
        width: max-content;
        margin-left: 25px;
        width: 30px;
        justify-content: flex-end;
        position: relative;
      }
    }

    .left-controls {
      justify-content: flex-start;
      align-items: center;
    }

    .center-controls {
      justify-content: center;
      align-items: center;
    }

    .right-controls {
      justify-content: flex-end;
      align-items: center;

      .time-display {
        display: flex;
        flex-direction: row;

        @include mobile-only {
          display: none;
        }
      }
    }
  }
}
