@import '../../../common//styles/styles.scss';
@import '../../../common/styles/responsive';

.dotstyle {
  position: fixed;
  top: 10%;
  right: 25px;
  z-index: 999999999;
  animation: opacity 0.5s ease-in-out;
	&.hidden {
    display: none;
  }
  @include mobile-only {
    display: none;
  }
  ul {
    position: relative;
    display: inline-block;
    margin: 0;
    padding: 0;
    list-style: none;
    background-color: transparent;
    cursor: default;
    border-radius: 55px;
    padding: 25px 0px;
    li {
      position: relative;
      display: block;
      // float: left;
      margin: 8px 16px;
      width: 10px;
      height: 10px;
      cursor: pointer;
      a {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        outline: none;
        border-radius: 50%;
        background-color: gray;
        text-indent: -999em;
        cursor: pointer; /* make the text accessible to screen readers */
        position: absolute;
      }
    }
  }
  ul:hover {
    opacity: 1 !important;
  }
}
/* Fill up */
.dotstyle-fillup {
  li {
    a {
      overflow: hidden;
      background-color: rgba(0, 0, 0, 0);
      transition: background 0.3s;
    }
    a::after {
      content: "";
      position: absolute;
      bottom: 0;
      height: 0;
      left: 0;
      width: 100%;
      background-color: red;
      box-shadow: 0 0 1px red;
      transition: height 0.3s;
    }
    a:hover,
    a:focus {
      background-color: rgba(0, 0, 0, 0.2);
    }
  }
  li.current a::after {
    height: 100%;
  }
}
/* SVG draw circle stroke */
.dotstyle-drawcircle {
  li {
    width: 18px;
    height: 18px;
    a {
      top: 3px;
      left: 3px;
      width: 12px;
      height: 12px;
      background-color: #c44d48;
      -webkit-transition: opacity 0.3s;
      transition: opacity 0.3s;
    }
    svg {
      z-index: 10;
      circle {
        opacity: 0;
        fill: none;
        stroke: red;
        stroke-width: 3;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-dasharray: 39 39;
        stroke-dashoffset: 39; /* ~ length of circle path (pi*2r) */
        transition: stroke-dashoffset 0.3s, opacity 0.3s;
      }
    }
  }
  li.current {
    a,
    a:hover,
    a:focus {
      opacity: 0.5;
    }
    svg circle {
      opacity: 1;
      stroke-dashoffset: 0;
      transition: stroke-dashoffset 0.3s, opacity 0.15s;
    }
  }
}
